'use client';

import { useState, useEffect } from 'react';
import Avatar from '../../components/Avatar';
import Image from 'next/image';

type UserProfile = {
  id: number;
  balance_crystals: number;
};

type Referral = {
  id: number;
  first_name: string;
  last_name: string | null;
};

const MY_ICON_PATH = '/images/134.png';

export default function FriendsPage() {
  const [user, setUser] = useState<UserProfile | null>(null);
  const [referrals, setReferrals] = useState<Referral[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);
  const [showRules, setShowRules] = useState(false); // –î–ª—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞

  useEffect(() => {
    const tg = window.Telegram?.WebApp;
    if (tg) {
      tg.ready();
      const initData = tg.initData;
      fetch('/api/auth', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ initData }),
      })
        .then((response) => {
          if (!response.ok) throw new Error(`–û—à–∏–±–∫–∞ –∞—É—Ç–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ü–∏–∏: ${response.statusText}`);
          return response.json();
        })
        .then((userData) => {
          if (userData && userData.error) {
            throw new Error(userData.error);
          }
          setUser(userData);
          return fetch('/api/referrals', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ initData }),
          });
        })
        .then((response) => {
          if (!response.ok) throw new Error(`–û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –¥—Ä—É–∑–µ–π: ${response.statusText}`);
          return response.json();
        })
        .then((referralsData) => {
          setReferrals(referralsData || []);
        })
        .catch((err) => {
          console.error('Fetch error on friends page:', err);
          setError(err.message || '–ù–µ —É–¥–∞–ª–æ—Å—å –∑–∞–≥—Ä—É–∑–∏—Ç—å –¥–∞–Ω–Ω—ã–µ.');
        })
        .finally(() => {
          setLoading(false);
        });
    } else {
      setError('Telegram WebApp –Ω–µ –Ω–∞–π–¥–µ–Ω. –û—Ç–∫—Ä–æ–π—Ç–µ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ –≤ Telegram.');
      setLoading(false);
    }
  }, []);

  const handleInviteClick = () => {
    const tg = window.Telegram?.WebApp;
    if (!tg || !user || !user.id) {
      tg?.showAlert('–ù–µ —É–¥–∞–ª–æ—Å—å —Å–æ–∑–¥–∞—Ç—å —Å—Å—ã–ª–∫—É. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–µ—Ä–µ–∑–∞–≥—Ä—É–∑–∏—Ç—å —Å—Ç—Ä–∞–Ω–∏—Ü—É.');
      return;
    }

    const botUsername = process.env.NEXT_PUBLIC_TELEGRAM_BOT_USERNAME;
    const botName = process.env.NEXT_PUBLIC_TELEGRAM_BOT_NAME;

    if (!botUsername || !botName) {
      console.error('Bot username or app name is not set in .env.local');
      tg?.showAlert('–û—à–∏–±–∫–∞ –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏–∏ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è.');
      return;
    }

    const referralLink = `https://t.me/${botUsername}/${botName}?startapp=ref_${user.id}`;
    const shareText = `–ü—Ä–∏–≤–µ—Ç! –ü—Ä–∏—Å–æ–µ–¥–∏–Ω—è–π—Å—è –∫ "–ê—Å—Å–∏—Å—Ç+" –∏ –ø–æ–ª—É—á–∞–π –±–æ–Ω—É—Å—ã. –ü–æ–º–æ–∂–µ–º –¥—Ä—É–≥ –¥—Ä—É–≥—É –Ω–∞–π—Ç–∏ –∫—Ä—É—Ç—ã–µ –∑–Ω–∞–∫–æ–º—Å—Ç–≤–∞ –∏ –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç–∏!`;
    const shareUrl = `https://t.me/share/url?url=${encodeURIComponent(referralLink)}&text=${encodeURIComponent(shareText)}`;

    tg.openTelegramLink(shareUrl);
  };

  const iconMaskStyle = (iconPath: string) => ({
    maskImage: `url(${iconPath})`,
    WebkitMaskImage: `url(${iconPath})`,
    maskSize: 'contain',
    maskRepeat: 'no-repeat',
    maskPosition: 'center',
  });

  if (loading) {
    return <div className="flex justify-center items-center h-screen bg-white">–ó–∞–≥—Ä—É–∑–∫–∞...</div>;
  }

  if (error) {
    return <div className="p-4 text-center text-red-500">–û—à–∏–±–∫–∞: {error}</div>;
  }

  return (
    <div className="flex flex-col min-h-screen font-['Unbounded'] bg-white">
      {/* –ö–Ω–æ–ø–∫–∞ —Å –ø—Ä–∞–≤–∏–ª–∞–º–∏ */}
      <div className="px-6 pt-4 pb-2">
        <button
          onClick={() => setShowRules(true)}
          className="w-full text-left text-sm text-blue-500 underline font-medium"
        >
          –£—Å–ª–æ–≤–∏—è —Ä–æ–∑—ã–≥—Ä—ã—à–µ–π –∏ –±–æ–Ω—É—Å–æ–≤
        </button>
      </div>

      {/* –û—Å–Ω–æ–≤–Ω–æ–π –∫–æ–Ω—Ç–µ–Ω—Ç */}
      <main className="flex-grow flex flex-col items-center px-6 pt-0 pb-4">

        <Image
          src={MY_ICON_PATH}
          alt="–ë–æ–Ω—É—Å–Ω—ã–π –∫—Ä–∏—Å—Ç–∞–ª–ª"
          width={180}
          height={180}
        />

        <h1 className="text-2xl text-black font-extrabold leading-tight mb-6">
          –ü—Ä–∏–≥–ª–∞—à–∞–π<br />–¥—Ä—É–∑–µ–π –∏ –ø–æ–ª—É—á–∞–π<br />–ø–ª—é—Å—ã
        </h1>

        {/* –ë–ª–æ–∫ –ø—Ä–∏–≥–ª–∞—à—ë–Ω–Ω—ã—Ö ‚Äî –ø—Ä–∏–º–µ—Ä–Ω–æ –Ω–∞ —á–µ—Ç–≤–µ—Ä—Ç—å —ç–∫—Ä–∞–Ω–∞ */}
        <div className="w-full max-w-sm mb-6 bg-gray-50 rounded-2xl p-4 border border-gray-100">
          <h2 className="text-lg font-bold text-black mb-3 text-left">
            –í—ã –ø—Ä–∏–≥–ª–∞—Å–∏–ª–∏: <span className="text-red-500">{referrals.length}</span>
          </h2>

          {referrals.length > 0 ? (
            <div
              className="flex flex-nowrap items-center overflow-x-auto py-2 
                         pl-1 pr-4
                         space-x-[-16px]
                         [&::-webkit-scrollbar]:hidden [-ms-overflow-style:none] [scrollbar-width:none]"
            >
              {referrals.map((ref, index) => (
                <Avatar
                  key={ref.id}
                  firstName={ref.first_name}
                  lastName={ref.last_name}
                  style={{ zIndex: referrals.length - index }}
                />
              ))}
            </div>
          ) : (
            <p className="text-gray-500 text-sm text-left">–ü–æ–∫–∞ –Ω–∏–∫—Ç–æ –Ω–µ –ø—Ä–∏—Å–æ–µ–¥–∏–Ω–∏–ª—Å—è</p>
          )}
        </div>
      </main>

        {/* 
        // üî≥ –ü—Ä–æ–≥—Ä–µ—Å—Å-–±–∞—Ä (–≤—Ä–µ–º–µ–Ω–Ω–æ –∑–∞–∫–æ–º–º–µ–Ω—Ç–∏—Ä–æ–≤–∞–Ω)
        <div className="w-full max-w-sm mb-6">
          <h3 className="text-sm font-bold text-black mb-2 text-left">–í–∞—à –ø—Ä–æ–≥—Ä–µ—Å—Å</h3>
          <div className="w-full bg-gray-200 rounded-full h-2.5">
            <div
              className="bg-red-500 h-2.5 rounded-full transition-all duration-300"
              style={{ width: `${Math.min((referrals.length / 30) * 100, 100)}%` }}
            ></div>
          </div>
          <p className="text-xs text-gray-500 text-left mt-1">
            {referrals.length} / 30 –ø—Ä–∏–≥–ª–∞—à–µ–Ω–∏–π
          </p>
        </div> 
        */}
      {/* –§—É—Ç–µ—Ä —Å –∫–Ω–æ–ø–∫–æ–π */}
      <footer className="w-full px-6 pb-6 flex-shrink-0">
        <button
          onClick={handleInviteClick}
          className="w-full h-16 flex items-center justify-center bg-red-500 text-white text-lg gap-2 font-medium rounded-2xl 
                     transition-all
                     shadow-[0_6px_0_0_rgba(0,0,0,0.3)] 
                     active:translate-y-1 active:shadow-[0_2px_0_0_rgba(0,0,0,0.3)]"
        >
          <span>–ü—Ä–∏–≥–ª–∞—Å–∏—Ç—å –¥—Ä—É–≥–∞</span>
          <div
            className="w-5 h-5 bg-white"
            style={iconMaskStyle(MY_ICON_PATH)}
          />
          <span>500</span>
        </button>
      </footer>

      {/* –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ —Å –ø—Ä–∞–≤–∏–ª–∞–º–∏ */}
      {showRules && (
        <div
          className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4"
          onClick={() => setShowRules(false)}
        >
          <div
            className="bg-white rounded-2xl p-5 max-w-sm w-full animate-fadeIn"
            onClick={(e) => e.stopPropagation()}
          >
            <h3 className="text-lg font-bold text-black mb-3">–£—Å–ª–æ–≤–∏—è —É—á–∞—Å—Ç–∏—è –∏ –ø—Ä–∏–∑—ã</h3>
            <ul className="text-sm text-gray-700 space-y-2">
              <li>‚Ä¢ <strong>10 –ø—Ä–∏–≥–ª–∞—à–µ–Ω–∏–π</strong> ‚Äî –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å –ø–æ–ø–∞—Å—Ç—å –Ω–∞ –æ–Ω–ª–∞–π–Ω –º–∏–Ω–∏-—Ä–∞–∑–±–æ—Ä —Å –ò–≤–∞–Ω–æ–º –ê–±—Ä–∞–º–æ–≤—ã–º. –†–∞–∑–±–æ—Ä –ø—Ä–æ–≤–æ–¥–∏—Ç—Å—è –µ–∂–µ–Ω–µ–¥–µ–ª—å–Ω–æ.</li>
              <li>‚Ä¢ <strong>20 –ø—Ä–∏–≥–ª–∞—à–µ–Ω–∏–π</strong> ‚Äî –ø—Ä–∏–æ—Ä–∏—Ç–µ—Ç–Ω–æ–µ –º–µ—Å—Ç–æ –Ω–∞ –º–∏–Ω–∏-—Ä–∞–∑–±–æ—Ä–µ, —á—Ç–æ –≥–∞—Ä–∞–Ω—Ç–∏—Ä—É–µ—Ç 100% —É—á–∞—Å—Ç–∏–µ –≤ –±–ª–∏–∂–∞–π—à–µ–π —Å–µ—Å—Å–∏–∏.</li>
              <li>‚Ä¢ <strong>30 –ø—Ä–∏–≥–ª–∞—à–µ–Ω–∏–π</strong> ‚Äî —É—á–∞—Å—Ç–∏–µ –≤ –µ–∂–µ–º–µ—Å—è—á–Ω–æ–º —Ä–æ–∑—ã–≥—Ä—ã—à–µ –∑–∞–≤—Ç—Ä–∞–∫–∞ —Å –ò–≤–∞–Ω–æ–º –ê–±—Ä–∞–º–æ–≤—ã–º –≤ –°–∫–æ–ª–∫–æ–≤–æ.</li>
            </ul>
            <button
              onClick={() => setShowRules(false)}
              className="mt-4 w-full py-2 bg-gray-200 text-black font-medium rounded-xl"
            >
              –ü–æ–Ω—è—Ç–Ω–æ
            </button>
          </div>
        </div>
      )}

      <style jsx>{`
        @keyframes fadeIn {
          from {
            opacity: 0;
            transform: translateY(10px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        .animate-fadeIn {
          animation: fadeIn 0.2s ease-out;
        }
      `}</style>
    </div>
  );
}